:root {
    --step-x: 10px;
    --step-y: 10px;
    --step-deg: 4deg;
}

.no_select {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

@keyframes floatMenacingly {
    0% {
        transform: translate(0, 0);
        filter: drop-shadow(0 0px 20px #252525);
    }
    50% {
        transform: translatey(-10px);
        filter: drop-shadow(0 5px 50px #252525);
    }
    100% {
        transform: translate(0, 0);
        filter: drop-shadow(0 0px 20px #252525);
    }

}

.hero_spacer {
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    transition: all 0.5s;
}

.main {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 0.5s;
}

.main h1 {
    color: lightgreen;
    font-size: 15rem;
}

.main h3 {
    margin-top: 0.8rem;
    text-wrap: none;
    color: lightgreen;
    font-size: 7rem;
    animation: 4s infinite floatMenacingly;
}

.hero_spacer.scrolled {
    height: 20vh;
}

.main.scrolled {
    transform: translateY(-100rem);
}

@media screen and (max-width: 768px) {

    .main h1 {
        color: lightgreen;
        font-size: 3rem;
    }

    .main h3 {
        text-wrap: none;
        color: lightgreen;
        font-size: 2rem;
        animation: 4s infinite floatMenacingly;
    }
}