.card {
    display: flex;
    position: relative;
    flex-direction: row;
    align-items: center;

    max-width: 50vw;
    border-radius: 1rem;
    padding: 2rem;
    transition: all 0.3s;

    background: #252525;
    color: #FFFDFA;
}

.card:hover {
    box-shadow: 0 0 2rem lightgreen;
    transform: translateY(-1rem);
}

.card * {
    padding: 2rem;
}

.card img {
    max-width: 15rem;
    object-fit: cover;
}

.card h4 {
    font-size: 3rem;
}

.card div p {
    font-size: 2rem;
    width: auto;
}

@media screen and (max-width: 900px) {
    .card {
        flex-direction: column;
    }

    .card * {
        padding: 1rem;
    }

    .card h4 {
        text-align: center;
    }

    .card img {
        max-height: 10rem;
        object-fit: contain;
    }
}

@media screen and (max-width: 768px) {
    .card {
        max-width: 90vw;
        border-radius: 1rem;
    }

}