.main {
    display: flex;
    flex-direction: row;
}

.main .body {
    width: 50vw;
}

.main .images {
    height: 150vh;
    position: relative;
}

.main .images img {
    position: absolute;
    object-fit: cover;
    max-width: 24vw;
}

.main .images .first {
    z-index: 3;
    transform: translate(0, 0);
}

.main .images .second {
    z-index: 2;
    transform: translate(16vw, 20vw);
}

.main .images .third {
    z-index: 1;
    transform: translate(5vw, 40vw);
}

@media screen and (max-width: 768px) {

    .main {
        margin-left: 3rem;
        flex-direction: column;
        align-items: flex-start;
    }

    .main .body {
        width: 75vw;
    }

    .main .images {
        width: 75vw;
        height: 25rem;
    }

    .main .images:hover {
        height: 80rem;
    }
    .main .images:hover .second {
        transform: translate(5rem, 60vw);
    }
    .main .images:hover .third {
        transform: translateY(120vw);
    }

    .main .images img {
        max-width: 70vw;
    }

    .main .images .first,
    .main .images .second,
    .main .images .third {
        width: 70vw;
        transform: translate(0, 0);
    }



}