.main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.grid {
    width: 80%;
    justify-content: center;
    align-content: center;
    display: flex;
    flex-direction: column;
    gap: 3rem;
    flex-wrap: wrap-reverse;
}

.grid p {
    flex: 1 1;
}