.paragraph {
    margin: 3rem;
    color: #FFFDFA;

    height: max-content;
}

.paragraph.hidden h3,
.paragraph.hidden p {
    transform: translateX(-100%);
}

.paragraph.hidden_right h3,
.paragraph.hidden_right p {
    left: 0;
    transform: translateX(100%);
}

.paragraph h3 {
    font-size: 8rem;
    color: antiquewhite;
    transition: all 0.9s;
}

.paragraph p {
    font-size: 3rem;
    transition: all 2s;
}

@media screen and (max-width: 768px) {

    .paragraph h3 {
        color: lightcoral;
        font-size: 3rem;
    }

    .paragraph p {
        font-size: 1.5rem;
    }
}