.main {
    width: 100vw;
    display: flex;
    gap: 2rem;
    align-items: center;
    justify-content: center;
}

.main img {
    object-fit: contain;
    height: 30rem;
    width: 30rem;

}

@media screen and (max-width: 768px) {

    .main {
        flex-direction: column;
    }

    .main img {
        width: 15rem;
        height: 15rem;
    }
}